import ComponentRoutes from './component'
import StyleRoutes from './style'

const router = [
  {
    path: '',
    redirect: 'home'
  },
  
  {
    path: '/home',
    name: 'ZHome',
    meta: {
      title: '',
      unique: 'home'
    },
    component: () => import('../views/Home')
  },

  {
    path: '/form',
    name: 'ZDemoForm',
    meta: {
      title: '',
      unique: 'form'
    },
    component: () => import('../views/Form'),
    children: [
      {
        path: '/form',
        redirect: 'rules'
      },

      {
        path: 'rules',
        name: 'ZDemoRules',
        meta: {
          title: 'Rules ',
          unique: 'rules',
          parent: 'form'
        },
        component: () => import('../views/Form/Rules')
      },

      {
        path: 'examples',
        name: 'ZDemoExamples',
        meta: {
          title: 'Examples ',
          unique: 'examples',
          parent: 'form'
        },
        component: () => import('../views/Form/Examples')
      },
    ]
  },

  {
    path: '/table',
    name: 'ZDemoTable',
    meta: {
      title: '',
      unique: 'table'
    },
    component: () => import('../views/Table')
  },

  ComponentRoutes,
  StyleRoutes
]

export { router }
export default router